.MoviePreviewThumb {
  position: relative;
  height: 300px;
}

.MoviePreviewThumb > img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.MoviePreviewTitle {
  position: absolute;
  top: 5px;
  left: 5px;
  color: aliceblue;
  background-color: rgba(0, 0, 0, 0.5);
}
